.model,
.overlay {
    border: none;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 200;
}

.overlay {
    background-color: rgba(124, 124, 124, 0.195);
}

.childDiv {
    border-radius: 0.5rem;
    background-color: var(--background);
    box-shadow: 0 0 0.125rem 0 rgba(0, 0, 0, 0.2), 0 0.125rem 0.1875rem 0.06rem rgba(0, 0, 0, 0.24);
    position: absolute;
    top: 50%;
    left: 50%;
    /* min-width: max-content; */
    transform: translate(-50%, -50%);
    z-index: 201;
    color: rgb(35, 35, 35);
}
